import uniLoadMore from '@/components/UniLoadMore/UniLoadMore.vue';
export default {
	components: {
		uniLoadMore,
	},
	data(){
		return {
			commentListItem:[],
			page:1,
			options:null,
			moreStatus:true
		}
	},
	onLoad(options){
		this.options = options;
		// 可分享当前页面
		uni.showShareMenu({
            withShareTicket:true,
        });
	},
	onShow(){
		// 每次页面显示都要重新归零,防止用户提交评价后无法及时查看
		this.commentListItem = [];
		this.page = 1;
		this.moreStatus = true;
		this.requestEval()
	},
	onReachBottom(){
		if(this.moreStatus){
			this.requestEval();
		}
	},
	methods:{
		requestEval(options){
			 // 商品评论
			this.$request.GET({
				url: this.$api.apiUrl.GET_GOODS_COMMENT,
				data: {
					id: this.options.id,
					page: this.page++
				}
			}).then(res => {
				if(res.data.length){
					this.commentListItem = this.commentListItem.concat(res.data);
				}else{
					this.moreStatus = false;
				}
			});
		}
	}
}
